/* SettingsPage.css */

.settings-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  font-family: Arial, sans-serif;
  background-color: #f3f4f6;
}

h2 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 10px;
}

.settings-description {
  color: #666;
  font-size: 1rem;
  margin-bottom: 20px;
  text-align: center;
}

.settings-options {
  width: 100%;
  max-width: 500px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.settings-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.settings-item label {
  font-weight: bold;
  color: #555;
  margin-bottom: 5px;
}

.settings-input {
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  outline: none;
  transition: border-color 0.3s;
}

.settings-input:focus {
  border-color: #0073e6;
}

.save-button {
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #0073e6;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.save-button:hover {
  background-color: #005bb5;
}
/* Media Queries */
@media (max-width: 768px) {
  .settings-page {
    padding: 20px 10px;
  }

  .settings-options {
    padding: 15px;
  }

  h2 {
    font-size: 1.5rem;
  }

  .save-button {
    padding: 10px;
    font-size: 0.9rem;
  }
}