/* Sezione principale */
.category-detail-section {
  padding: 40px 20px;
  background-color: #fbfbfb;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Titolo principale */
.category-detail-title {
  font-size: 2.4rem;
  font-weight: 700;
  margin-bottom: 25px;
  color: #333;
  text-align: center;
  border-bottom: 2px solid #ff9900;
  padding-bottom: 10px;
  width: 100%;
  max-width: 1000px;
}

/* Griglia dei prodotti */
.category-detail-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding-top: 20px;
  width: 100%;
  align-items: stretch;
}

/* Card del prodotto */
.category-detail-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  cursor: pointer;
}

.category-detail-card:hover {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  transform: translateY(-5px);
}

/* Immagine del prodotto */
.category-detail-image {
  width: 100%;
  height: 250px;
  object-fit: contain;
  margin-bottom: 15px;
  transition: transform 0.3s ease;
}

.category-detail-card:hover .category-detail-image {
  transform: scale(1.05);
}

/* Nome del prodotto */
.category-detail-product-name {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 10px;
  font-weight: 600;
  text-align: center;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Descrizione del prodotto */
.category-detail-product-description {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 15px;
  height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Prezzo del prodotto */
.category-detail-price {
  font-size: 1.2rem;
  color: #ff9900;
  font-weight: bold;
  margin-bottom: 10px;
}

/* Prezzo originale barrato */
.originalPrice {
  color: #999;
  font-size: 0.9rem;
  text-decoration: line-through;
  margin-right: 10px;
}

/* Percentuale di sconto */
.discountPercentage {
  color: #d9534f;
  font-size: 0.9rem;
  font-weight: bold;
}

/* Controlli della quantità */
.quantity-control {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 10px;
  margin-bottom: 15px; /* Aggiunto margine inferiore */
}

.quantity-btn {
  background-color: #eee;
  border: 1px solid #ddd;
  padding: 5px 10px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.quantity-btn:hover {
  background-color: #ccc;
}

.quantity-display {
  font-size: 1rem;
  padding: 0 5px;
}

/* Pulsante acquisto */
.category-detail-buy-button {
  background: linear-gradient(to right, #ff9900, #ff6600);
  color: white;
  padding: 12px 20px;
  border-radius: 5px;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.9rem;
  transition: background 0.3s ease;
  border: none;
  cursor: pointer;
  width: 100%;
  max-width: 200px;
  align-self: center;
  margin-top: 15px; /* Margine superiore per separare dal controllo quantità */
}

.category-detail-buy-button:hover {
  background: linear-gradient(to right, #ff6600, #ff3300);
}

/* Media query per dispositivi mobili */
@media screen and (max-width: 768px) {
  .category-detail-title {
    font-size: 2rem;
  }
  .category-detail-list {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 15px;
  }
  .category-detail-image {
    height: 180px;
  }
}