/* Contenitore principale */
.custom-product-list__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 15px;
  background-color: #fbfbfb;
  max-width: 1200px;
  margin: 0 auto;
}

/* Titolo delle sezioni */
.custom-product-list__title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: #333333;
  text-align: left;
  border-bottom: 2px solid #f3a847;
  padding-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
}

/* Griglia dei prodotti */
.custom-product-list__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
  padding: 0 10px;
}

/* Card del prodotto */
.custom-product-list__grid > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  padding: 15px;
  width: calc(20% - 20px);
  min-height: 400px; /* Altezza minima per uniformare le card */
}

.custom-product-list__grid > div:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

/* Immagine del prodotto */
.custom-product-list__grid > div > img {
  width: 100%;
  height: 200px;
  object-fit: contain;
  border-radius: 10px 10px 0 0;
  margin-bottom: 15px;
  transition: transform 0.3s ease;
}

.custom-product-list__grid > div:hover img {
  transform: scale(1.05);
}

/* Dettagli del prodotto */
.custom-product-list__grid > div > .info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex-grow: 1;
  width: 100%;
  text-align: center;
}

/* Nome del prodotto */
.custom-product-list__grid > div > .info > h3 {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  min-height: 50px;
}

/* Descrizione del prodotto */
.custom-product-list__grid > div > .info > p {
  font-size: 14px;
  color: #666666;
  margin-bottom: 10px;
  min-height: 60px;
}

/* Prezzo del prodotto */
.custom-product-list__grid > div > .info > .price {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #e67e22;
}

/* Pulsante 'Aggiungi al Carrello' */
.custom-product-list__grid > div > .info > button {
  background-color: #ffd700;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 10px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.custom-product-list__grid > div > .info > button:hover {
  background-color: #ffc400;
  transform: translateY(-3px);
}

/* Media Queries per la reattività */

@media (max-width: 868px) {
  .custom-product-list__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
  padding: 0 0px;
}
  /* Griglia dei prodotti */
  .custom-product-list__grid > div {
    width: calc(33.33% - 15px); /* Tre colonne */
    min-height: 320px; /* Altezza minima per simmetria */
  }

  /* Immagine del prodotto */
  .custom-product-list__grid > div > img {
    height: 180px;
    object-fit: contain; /* Mostra l'immagine intera senza tagliarla */
    margin-bottom: 10px; /* Spaziatura tra immagine e testo */
  }

  /* Nome del prodotto */
  .custom-product-list__grid > div > .info > h3 {
    font-size: 1rem;
    margin-bottom: 8px; /* Spaziatura uniforme */
    line-height: 1.2; /* Leggibilità migliorata */
  }

  /* Descrizione del prodotto */
  .custom-product-list__grid > div > .info > p {
    font-size: 0.9rem;
    margin-bottom: 10px; /* Spazio coerente */
    color: #555; /* Testo più chiaro per leggibilità */
  }

  /* Prezzo del prodotto */
  .custom-product-list__grid > div > .info > .price {
    font-size: 1rem;
    margin-bottom: 12px;
    font-weight: bold;
    color: #e67e22; /* Evidenzia il prezzo */
  }
