/* Responsive fino a 480px */
@media (max-width: 480px) {
  /* Griglia dei prodotti */
  .custom-product-list__grid > div {
    width: 60%; /* Card occupa il 60% della pagina */
    margin: 0 auto; /* Centra la card sull'asse X */
    min-height: 300px; /* Altezza minima uniforme */
  }

  /* Immagine del prodotto */
  .custom-product-list__grid > div > img {
    height: 160px; /* Altezza ridotta per adattarsi */
    object-fit: contain; /* Mostra sempre l'immagine intera */
    margin-bottom: 10px;
  }

  /* Nome del prodotto */
  .custom-product-list__grid > div > .info > h3 {
    font-size: 0.9rem;
    margin-bottom: 6px; /* Riduce spazi per ottimizzare spazio */
    text-align: center; /* Allinea il testo al centro */
  }

  /* Descrizione del prodotto */
  .custom-product-list__grid > div > .info > p {
    display: none; /* Nasconde la descrizione per ottimizzare lo spazio */
  }

  /* Prezzo del prodotto */
  .custom-product-list__grid > div > .info > .price {
    font-size: 0.95rem;
    margin-bottom: 8px;
    font-weight: bold;
    color: #e67e22;
    text-align: center; /* Allinea il prezzo al centro */
  }

  /* Pulsante 'Aggiungi al Carrello' */
  .custom-product-list__grid > div > .info > button {
    background-color: #ffd700;
    color: #ffffff;
    border: none;
    padding: 10px;
    font-size: 0.85rem; /* Riduce il font per dispositivi più piccoli */
    cursor: pointer;
    border-radius: 8px;
    width: 100%; /* Pulsante a tutta larghezza */
    text-align: center;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }

  /* Hover sul pulsante */
  .custom-product-list__grid > div > .info > button:hover {
    background-color: #ffc400;
    transform: translateY(-2px);
  }
}
