/* Variabili globali per colori e dimensioni */
:root {
  --primary-color: #ff8c00;
  --primary-hover: #e67800;
  --secondary-color: #34495e;
  --danger-color: #e74c3c;
  --danger-hover: #c0392b;
  --success-color: #2ecc71;
  --disabled-color: #bdc3c7;
  --background-light: #f5f5f5;
  --box-shadow: rgba(0, 0, 0, 0.1);
  --box-shadow-hover: rgba(0, 0, 0, 0.2);
  --border-radius: 10px;
  --transition: 0.3s ease;
}

/* Reset base */
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  background-color: var(--background-light);
}

/* Contenitore principale */
.cartContainer {
  max-width: 1100px;
  margin: 40px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: var(--border-radius);
  box-shadow: 0 8px 20px var(--box-shadow);
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Titolo del carrello */
.cartContainer h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: var(--secondary-color);
  font-weight: bold;
}

/* Pulsante Rimuovi tutti i prodotti */
.removeAllBtn {
  align-self: flex-end;
  background-color: var(--danger-color);
  color: #fff;
  padding: 12px 20px;
  border: none;
  border-radius: var(--border-radius);
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
  box-shadow: 0 4px 12px var(--box-shadow);
}

.removeAllBtn:hover {
  background-color: var(--danger-hover);
  box-shadow: 0 6px 20px var(--danger-hover);
}
.removeBtn {
  background-color: var(--danger-color);
  color: #fff;
  padding: 8px 15px;
  border: none;
  border-radius: var(--border-radius);
  font-size: 0.9rem;
  cursor: pointer;
  transition: var(--transition);
  box-shadow: 0 4px 12px var(--box-shadow);
}
/* Contenitore articoli */
.cartItemsContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Articolo nel carrello */
.cartItem {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 16px var(--box-shadow);
  gap: 20px;
  transition: var(--transition);
  position: relative;
}

.cartItem:hover {
  transform: scale(1.02);
  box-shadow: 0 6px 24px var(--box-shadow-hover);
}

/* Pulsante Rimuovi */
.removeItemBtn {
  background-color: var(--danger-color);
  color: #fff;
  padding: 8px 15px;
  border: none;
  border-radius: var(--border-radius);
  font-size: 0.9rem;
  cursor: pointer;
  transition: var(--transition);
  box-shadow: 0 4px 12px var(--box-shadow);
}

.removeItemBtn:hover {
  background-color: var(--danger-hover);
}

/* Immagine articolo (adesso visibile intera) */
.cartItemImage {
  width: 140px;
  height: 140px;
  object-fit: contain; /* Cambiato da cover per una visualizzazione completa dell'immagine */
  border-radius: var(--border-radius);
}

/* Dettagli articolo */
.itemDetails {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.itemName {
  font-size: 1.3rem;
  font-weight: bold;
  color: var(--secondary-color);
}

.itemSpecs {
  font-size: 0.9rem;
  color: #888;
}

.itemAvailability {
  font-size: 0.9rem;
  font-weight: bold;
}

.itemAvailability.available {
  color: var(--success-color);
}

.itemAvailability.unavailable {
  color: var(--danger-color);
}

/* Prezzo */
.itemPrice {
  font-size: 1.2rem;
  color: var(--primary-color);
  display: flex;
  align-items: center;
  gap: 10px;
}

.originalPrice {
  font-size: 0.9rem;
  color: #bbb;
  text-decoration: line-through;
}

.discountPercentage {
  background-color: #ffecd2;
  color: var(--primary-color);
  font-size: 0.8rem;
  padding: 2px 6px;
  border-radius: var(--border-radius);
}

/* Controllo quantità */
.quantityControl {
  display: flex;
  align-items: center;
  gap: 10px;
}

.quantityBtn {
  background-color: var(--primary-color);
  color: #fff;
  padding: 12px;
  border: none;
  border-radius: 50%;
  font-size: 1.4rem;
  cursor: pointer;
  transition: var(--transition);
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.quantityBtn:hover {
  background-color: var(--primary-hover);
}

.quantityBtn:disabled {
  background-color: var(--disabled-color);
  cursor: not-allowed;
}

/* Div Totale */
.cartTotal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, #f0f0f0, #ffffff);
  padding: 20px 30px;
  border-radius: var(--border-radius);
  box-shadow: 0 8px 20px var(--box-shadow);
  transition: var(--transition);
  flex-wrap: wrap;
  gap: 20px;
}

.cartTotal h3 {
  font-size: 1.5rem;
  color: var(--secondary-color);
  flex: 1;
  text-align: left;
}

.cartTotal span {
  font-size: 1.2rem;
  font-weight: bold;
  color: var(--primary-color);
}

.cartTotal .checkoutBtn {
  background-color: var(--primary-color);
  color: #fff;
  padding: 12px 25px;
  border: none;
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: var(--transition);
  box-shadow: 0 6px 18px var(--box-shadow);
}

.cartTotal .checkoutBtn:hover {
  background-color: var(--primary-hover);
}

.cartTotal .checkoutBtn:disabled {
  background-color: var(--disabled-color);
  color: #777;
  cursor: not-allowed;
  box-shadow: none;
}

/* Media Query */
@media screen and (max-width: 768px) {
  /* Contenitore carrello */
  .cartContainer {
    padding: 15px;
  }

  /* Titolo carrello */
  .cartContainer h2 {
    font-size: 1.6rem;
    text-align: center;
  }

  /* Contenitore articoli */
  .cartItemsContainer {
    gap: 15px;
  }

  /* Articolo nel carrello */
  .cartItem {
    flex-direction: column;
    align-items: center;
    gap: 15px;
    padding: 10px;
  }

  /* Immagine articolo: centrata e con margini per non essere troppo vicina ai bordi */
  .cartItemImage {
    width: 50%; /* Ridotto per dimensioni ancora più piccole */
    max-width: 135px; /* Impostato un limite massimo per l'immagine */
    height: auto;
    object-fit: contain; /* Mantiene la proporzione dell'immagine */
    margin: 9px 15px; /* Aggiunto margine laterale per separare dai bordi */
  }

  /* Dettagli articolo */
  .itemDetails {
    text-align: center;
    flex: 1;
    gap: 8px;
  }

  /* Nome articolo */
  .itemName {
    font-size: 1rem;
    font-weight: 600;
  }

  /* Specifiche articolo */
  .itemSpecs {
    font-size: 0.8rem;
    color: #777;
  }

  /* Disponibilità articolo */
  .itemAvailability {
    font-size: 0.9rem;
    color: #555;
  }

  /* Prezzo articolo centrato */
  .itemPrice {
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-direction: column;
  }

  /* Prezzo originale e sconto centrato */
  .originalPrice {
    font-size: 0.9rem;
    color: #bbb;
    text-decoration: line-through;
    display: block;
  }

  .discountPercentage {
    background-color: #ffecd2;
    color: var(--primary-color);
    font-size: 0.8rem;
    padding: 2px 6px;
    border-radius: var(--border-radius);
  }

  /* Controllo quantità (pulsanti + e - uno accanto all'altro) */
  .quantityControl {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
  }

  .quantityBtn {
    background-color: var(--primary-color);
    color: #fff;
    padding: 8px;
    border: none;
    border-radius: var(--border-radius);
    font-size: 1rem;
    cursor: pointer;
    transition: var(--transition);
    width: 40px; /* Impostato per uniformare la dimensione dei pulsanti */
    height: 40px; /* Impostato per uniformare la dimensione dei pulsanti */
  }

  .quantityBtn:hover {
    background-color: var(--primary-hover);
  }

  /* Pulsante rimuovi */
  .removeItemBtn {
    padding: 8px 20px;
    font-size: 1rem;
  }

  /* Pulsante rimuovi tutti */
  .removeAllBtn {
    
    padding: 10px 15px;
    font-size: 1rem;
  }

  /* Totale carrello */
  .cartTotal {
    flex-direction: column;
    gap: 20px;
    padding: 20px 25px;
  }

  .cartTotal h3 {
    text-align: center;
    font-size: 1.4rem;
  }

  .cartTotal span {
    font-size: 1.3rem;
    text-align: center;
  }

  /* Pulsante checkout */
  .cartTotal .checkoutBtn {
    width: 100%;
    padding: 12px;
    text-align: center;
  }
}
