/* Stile del banner della notifica - moderno e centrato in alto */
.notifBanner {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ffffff;
  color: #333;
  border-radius: 12px;
  box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.2);
  width: 500px;
  padding: 18px;
  display: flex;
  align-items: center;
  gap: 14px;
  cursor: pointer;
  transition: opacity 0.3s ease, transform 0.3s ease;
  z-index: 1000;
  animation: slideDown 0.3s forwards, fadeOut 0.3s ease 2.7s forwards;
}

.notifImage {
  width: 70px;
  height: 70px;
  object-fit: contain; /* Mostra l'immagine intera senza tagliarla */
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  background-color: #fff; /* Colore di sfondo per evitare bordi irregolari */
  padding: 4px; /* Spazio aggiuntivo per un aspetto più pulito */
}

/* Contenitore dei dettagli del prodotto */
.notifDetails {
  flex: 1;
  color: #2e3a59;
}

/* Titolo del prodotto e icona */
.notifTitle {
  font-size: 17px;
  font-weight: bold;
  color: #2e3a59;
  display: flex;
  align-items: center;
  gap: 4px;
}

.notifCheckIcon {
  color: #3fbf5b;
  font-size: 20px;
}

/* Prezzo e quantità */
.notifPriceLabel, .notifQuantityLabel {
  color: #333;
  font-weight: 600;
}

.notifPrice {
  font-size: 16px;
  margin-top: 4px;
  color: #b12704;
}

.notifOriginalPrice {
  font-size: 15px;
  color: #9b9b9b;
  text-decoration: line-through;
  margin-right: 6px;
}

.notifDiscountedPrice {
  font-size: 16px;
  color: #b12704;
  font-weight: bold;
}

.notifDiscount {
  background-color: #ffdd57;
  color: #b12704;
  padding: 3px 8px;
  font-size: 12px;
  margin-left: 6px;
  font-weight: bold;
  border-radius: 10px;
  display: inline-block;
}

/* Bottone di chiusura */
.notifCloseBtn {
  background: none;
  border: none;
  color: #888;
  font-size: 24px;
  cursor: pointer;
  transition: color 0.3s ease;
  position: absolute;
  right: 12px;
  top: 12px;
}

.notifCloseBtn:hover {
  color: #333;
}

/* Animazioni di entrata e uscita */
@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translate(-50%, -20%);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

@keyframes fadeOut {
  to {
    opacity: 0;
    transform: translate(-50%, -20%);
  }
}
@media (max-width: 480px) {
  .notifBanner {
    width: 90%;
    padding: 14px;
  }

  .notifImage {
    width: 50px;
    height: 50px;
  }

  .notifTitle {
    font-size: 0.9rem;
  }

  .notifPriceLabel, .notifDiscountedPrice {
    font-size: 0.85rem;
  }
}
