.footer {
  background-color: #e3f2fd;
  color: #38506e;
  padding: 40px 0;
  text-align: left;
  clear: both;
}

.footer .container {
  max-width: 1200px;
  margin: 0 auto;
}

.footer .row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer .col-md-3 {
  flex-basis: 22%;
  padding: 10px;
  min-width: 180px;
}

.footer h4 {
  margin-bottom: 15px;
  font-size: 16px;
  color: #38506e;
  text-transform: uppercase;
  font-weight: 600;
}

.footer ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer ul li {
  margin-bottom: 8px;
}

.footer ul li a {
  color: #38506e;
  text-decoration: none;
  display: flex;
  align-items: center;
  transition: color 0.3s ease;
}

.footer ul li a .fab,
.footer ul li a span {
  transition: color 0.3s ease;
}

.footer ul li a:hover .fab,
.footer ul li a:hover span {
  color: #f3a847;
}

.footer ul li a .fab {
  margin-right: 10px;
  font-size: 20px;
}

.footer .payment-icons .fab {
  margin-right: 15px;
  font-size: 26px;
}

.footer .row-copy {
  text-align: center;
  padding-top: 20px;
  border-top: 1px solid #444;
  margin-top: 20px;
  font-size: 14px;
}

.footer span {
  margin-left: 5px;
}

@media (max-width: 768px) {
  .product {
    max-width: 220px;
    padding: 10px;
    box-shadow: none;
    margin-bottom: 20px;
  }

  .product-image {
    width: 100%;
    height: 160px;
    margin-bottom: 8px;
    border-radius: 6px;
  }

  .product-name {
    font-size: 0.95rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 5px;
  }

  .product-description {
    font-size: 11px;
    margin-bottom: 6px;
    text-align: center;
  }

  /* Prezzo impilato verticalmente, centrato */
  .product-price-container {
    flex-direction: column;
    align-items: center;
    gap: 4px;
    margin-bottom: 10px;
  }

  .old-price {
    font-size: 12px;
  }

  .discounted-price {
    font-size: 14px;
    color: #B12704;
  }

  .discount-percentage {
    font-size: 10px;
    padding: 2px 4px;
  }

  /* Contenitore orizzontale per quantità e bottone */
  .product-action-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 8px;
  }

  .quantity-container {
    display: flex;
    align-items: center;
    gap: 4px;
  }

  .quantity-label {
    font-size: 0.85rem;
  }

  .quantity-select {
    width: 55px;
    padding: 3px;
    font-size: 0.85rem;
  }

  .add-to-cart {
    background-color: #FF9900;
    color: white;
    width: 60%;
    padding: 8px;
    font-size: 13px;
    font-weight: bold;
    border-radius: 4px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }

  .add-to-cart:hover {
    background-color: #FFB84D;
  }
}

@media (max-width: 480px) {
  .product {
    max-width: 100%;
    padding: 8px;
  }

  .product-image {
    height: 130px;
    margin-bottom: 6px;
  }

  .product-name {
    font-size: 0.85rem;
    margin-bottom: 4px;
  }

  .product-description {
    font-size: 10px;
    margin-bottom: 4px;
  }

  .old-price {
    font-size: 11px;
  }

  .discounted-price {
    font-size: 12px;
  }

  .discount-percentage {
    font-size: 9px;
    padding: 1px 3px;
  }

  .quantity-container {
    gap: 3px;
  }

  .quantity-select {
    width: 50px;
    padding: 2px;
  }

  .add-to-cart {
    width: 100%;
    padding: 6px;
    font-size: 12px;
  }
}