/* Contenitore principale della pagina di login */
.login-page-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(135deg, #1e90ff, #87cefa);
  padding: 20px;
  box-sizing: border-box;
}

/* Box di login */
.login-box {
  background-color: #ffffff;
  padding: 40px 30px;
  border-radius: 20px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 400px;
  width: 100%;
  animation: fadeIn 0.5s ease;
  overflow: hidden;
}

.login-box h1 {
  font-size: 2.2rem;
  font-weight: bold;
  color: #2c3e50;
  margin-bottom: 25px;
  word-wrap: break-word;
}

/* Messaggio di errore */
.login-error-message {
  color: #e74c3c;
  font-size: 1rem;
  margin-bottom: 15px;
  background-color: #fdecea;
  padding: 12px;
  border-radius: 8px;
  font-weight: 500;
  box-shadow: 0 2px 8px rgba(231, 76, 60, 0.1);
}

/* Form di login */
.login-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Gruppo del form (label e input) */
.form-group {
  text-align: left;
  width: 100%;
}

.form-group label {
  font-size: 1rem;
  font-weight: 600;
  color: #34495e;
  margin-bottom: 8px;
  display: block;
}

/* Campi di input */
.login-input {
  width: 100%;
  padding: 14px;
  font-size: 1rem;
  border: 2px solid #dcdcdc;
  border-radius: 10px;
  background-color: #fafafa;
  transition: border-color 0.3s, box-shadow 0.3s;
  box-sizing: border-box;
}

.login-input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 8px rgba(52, 152, 219, 0.3);
}

/* Bottone di login */
.login-button {
  width: 100%;
  padding: 14px;
  background: linear-gradient(135deg, #1e90ff, #4682b4);
  color: white;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.login-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(30, 144, 255, 0.4);
}

.login-button:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
}

/* Prompt di registrazione */
.register-prompt {
  margin-top: 20px;
  font-size: 0.9rem;
  color: #7f8c8d;
  text-align: center;
}

.register-prompt a {
  color: #1e90ff;
  font-weight: bold;
  text-decoration: none;
  transition: color 0.3s ease, text-decoration 0.3s ease;
}

.register-prompt a:hover {
  color: #104e8b;
  text-decoration: underline;
}

/* Animazione di dissolvenza */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Media query per tablet */
@media (max-width: 768px) {
  .login-box {
    padding: 30px 20px;
    border-radius: 16px;
  }

  .login-box h1 {
    font-size: 1.8rem;
  }

  .form-group label {
    font-size: 0.9rem;
  }

  .login-input {
    padding: 12px;
    font-size: 0.95rem;
  }

  .login-button {
    padding: 12px;
    font-size: 1rem;
  }

  .register-prompt {
    font-size: 0.85rem;
  }
}

/* Media query per schermi piccoli */
@media (max-width: 480px) {
  .login-box {
    padding: 25px 15px;
    border-radius: 14px;
  }

  .login-box h1 {
    font-size: 1.5rem;
  }

  .form-group label {
    font-size: 0.85rem;
  }

  .login-input {
    padding: 10px;
    font-size: 0.9rem;
  }

  .login-button {
    padding: 10px;
    font-size: 0.9rem;
  }

  .register-prompt {
    font-size: 0.8rem;
  }
}
