/* Contenitore generale della pagina profilo */
.profile-page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(135deg, #eef2f6, #ffffff);
  color: #333;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
}

/* Card profilo */
.profile-card-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.5s ease-in-out;
}

/* Header */
.profile-card-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  text-align: center;
  margin-bottom: 15px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 10px;
}

.profile-card-header h3 {
  font-size: 1.6rem;
  font-weight: bold;
  color: #222;
  word-break: break-word;
}

/* Pulsante modifica */
.profile-edit-button {
  padding: 10px 15px;
  background: linear-gradient(135deg, #0073e6, #005bb5);
  color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.profile-edit-button:hover {
  background: linear-gradient(135deg, #005bb5, #003f8a);
}

/* Informazioni */
.profile-card-info {
  text-align: center;
  line-height: 1.5;
}

.profile-card-info p {
  font-size: 1rem;
  color: #555;
}

/* Azioni */
.profile-account-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  width: 100%;
}
.profile-login-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(135deg, #eef2f6, #ffffff);
  color: #333;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
}

.profile-login-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.5s ease-in-out;
}
.profile-login-button {
  width: 100%;
  padding: 14px;
  background: linear-gradient(135deg, #1e90ff, #4682b4);
  color: white;
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.profile-action-button,
.profile-logout-button {
  padding: 10px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.profile-action-button {
  background: linear-gradient(135deg, #28a745, #218838);
}

.profile-action-button:hover {
  background: linear-gradient(135deg, #218838, #1e7e34);
}

.profile-logout-button {
  background: linear-gradient(135deg, #dc3545, #c82333);
}

.profile-logout-button:hover {
  background: linear-gradient(135deg, #c82333, #a71d2a);
}

/* Responsive */
@media (max-width: 768px) {
  .profile-card-container {
    width: 100%;
    padding: 15px;
  }

  .profile-edit-button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }

  .profile-account-actions {
    flex-direction: column;
  }

  .profile-action-button,
  .profile-logout-button {
    width: 100%;
  }
  .profile-login-container {
    padding: 15px;
    border: none;
    box-shadow: none;
    background-color: transparent;
  }
  .profile-login-description {
    font-size: 0.9rem;
    text-align: center;
  }
  .profile-login-button {
    font-size: 1rem;
    width: 50%;
  }
}

@media (max-width: 450px) {
  .profile-card-container {
    margin: 0 10px;
  }

  .profile-card-header h3 {
    font-size: 1.4rem;
  }
  .profile-login-container {
    padding: 15px;
    border: none;
    box-shadow: none;
    background-color: transparent;
  }
    .profile-login-description {
    font-size: 0.9rem;
    text-align: center;
  }
  .profile-card-info p {
    font-size: 0.9rem;
  }

  .profile-action-button,
  .profile-logout-button {
    font-size: 0.9rem;
  }
}
