/* PaymentConfirmation.css */
.payment-confirmation {
  background-color: #f8fff8;
  padding: 30px;
  border-radius: 10px;
  max-width: 600px;
  margin: 40px auto;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
  text-align: center;
  font-family: "Roboto", sans-serif;
}

.payment-confirmation h2 {
  font-size: 26px;
  color: #28a745;
}

.payment-confirmation p {
  font-size: 18px;
  color: #555;
}

.return-button {
  margin-top: 20px;
  padding: 14px 28px;
  font-size: 16px;
  font-weight: bold;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s;
}

.return-button:hover {
  background-color: #0056b3;
  transform: scale(1.02);
}
@media (max-width: 480px) {
  .payment-confirmation {
    padding: 15px;
    margin: 10px auto;
  }

  .payment-confirmation h2 {
    font-size: 1.3rem;
  }

  .payment-confirmation p {
    font-size: 0.9rem;
  }

  .return-button {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}