/* Loading.css */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Copre l'intera altezza della viewport */
  background-color: #f8fff8; /* Stessa tonalità di sfondo */
  font-family: "Roboto", sans-serif;
}

.loader {
  border: 8px solid #f3f3f3; /* Colore di sfondo */
  border-top: 8px solid #3498db; /* Colore del loader */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-container p {
  margin-top: 20px;
  font-size: 18px;
  color: #555;
}
@media (max-width: 768px) {
  .loading-container {
    padding: 20px;
  }

  .loader {
    width: 40px;
    height: 40px;
    border: 6px solid #f3f3f3;
    border-top: 6px solid #3498db;
  }

  .loading-container p {
    font-size: 16px;
  }
}
